.specialist-page {
  padding: 100px 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.specialist-page img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 30px;
}

.specialist-page h1 {
  font-size: 32px;
  margin-bottom: 15px;
}

.specialist-page p {
  font-size: 18px;
  color: #555;
  margin-bottom: 10px;
}

.specialists-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}

